import React, { useEffect, useState } from 'react';
import NavBar from '../components/NavBar'
import GoogleMapReact from 'google-map-react';
import { useHistory } from "react-router-dom";
import search from "../assests/search.svg";
import Api from '../api/api';
import {
    Accordion,
    AccordionItem,
    AccordionItemHeading,
    AccordionItemButton,
    AccordionItemPanel,
} from 'react-accessible-accordion';
import map from "../assests/map.svg";
const axios = require('axios');
const AnyReactComponent = ({ text }) => <div><img alt="Marker" src="https://img.icons8.com/fluent/48/000000/marker-storm.png" />{text}</div>;


function LiveView(props) {
    const [arrLiveView, setarrLiveView] = useState([]);
    const [isError, setIsError] = useState();
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [arrSplittedValue, setArrSplittedValue] = useState([]);
    const [showData, setShowData] = useState(false);
    const [arrTagNumber, setArrTagNumber] = useState([]);
    const [selectedLiveViewHdl, setselectedLiveViewHdl] = useState("");
    let token = localStorage.getItem("b_token");
    let prefixUrl = localStorage.getItem("prefixUrl");
    let baseUrl = `https://${prefixUrl}.pim360.io/api`;
    let arrData = [];
    useEffect(() => {
        fetchLiveView();
    }, [])

    const fetchLiveView = () => {
        setIsLoading(true);
        let url = `${baseUrl}/customviews?type=LIVE_VIEW`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: token,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            if (responseData !== undefined && responseData !== null) {
                setarrLiveView(responseData);
            }
            setIsLoading(false);
        }).catch(error => {
            console.log("error -------->" + error);
            setIsLoading(false);
        });
    };

    const fetchSelectedLiveViewData = (e, selectedLiveView, selectedHdl) => {
        setselectedLiveViewHdl(selectedLiveView);
        setIsLoading(true);
        let url = `${baseUrl}/customviews/${selectedHdl}`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: token,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(response => {
            if (response !== undefined && response !== null) {
                getResultUrl(response);
            }
        }).catch(error => {
            console.log("fetchSelectedLiveViewData error -------->" + error);
            setIsLoading(false);
        });
    }

    const getResultUrl = (response) => {
        let createQueryBody = {
            "type": "TAGGED_ITEM",
            "eic": "",
            "filter": response.Data.conditions,
            "fields": response.Data.fields
        };
        let url = `${baseUrl}/queries`;
        fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": token
            },
            body: JSON.stringify(createQueryBody),
        })
            .then(response => response.json())
            .then(data => {
                return getFinalResult(data.resulturl).then((finalResult) => {
                    setIsLoading(false);
                    let arrFirstObjKeys = Object.keys(finalResult[0]).filter(e => typeof finalResult[0][e] == 'object')
                    let arrModifiedData = [];
                    let arrTagNumberData = [];
                    finalResult.map((item) => {
                        let obj = {};
                        arrFirstObjKeys.map((key) => {
                            if (item[key].name == "Tag Number" || item[key].name == "TAG NUMBER") {
                                arrTagNumberData.push(item[key].value);
                            }
                            obj[item[key].name] = item[key].value;
                        });
                        arrModifiedData.push(obj);
                    });
                    if (arrModifiedData.length > 0) {
                        if (arrModifiedData[0].hasOwnProperty("Tag Number") || arrModifiedData[0].hasOwnProperty("TAG NUMBER")) {
                            if (arrTagNumberData.length > 0) {
                                setArrTagNumber(arrTagNumberData);
                            }
                            setShowData(true);
                        }
                        else {
                            //finish process and show no data found
                            setShowData(true)
                        }
                    }
                });
            })
            .catch((error) => {
                console.error('Error:', error);
                setIsLoading(false)
            });
    };

    const getFinalResult = (queryResult, pageSize = 200, page = 0) => {
        let url = `${baseUrl}${queryResult}?limit=${pageSize}&skip=${page}`
        axios.defaults.headers.common = { 'Authorization': localStorage.getItem("b_token") };
        return Api.getRequest(url).then((response) => {
            arrData.push(...response.data);
            if (response.hasmore) {
                return getFinalResult(queryResult, pageSize, (arrData.length / pageSize) * 200)
            } else {
                setIsLoading(false);
                return arrData;
            }
        });
    }

    const getAttributes = (selectedTag) => {
        let url = `${baseUrl}/objects/TAGGED_ITEM/id/${selectedTag}`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: token,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            setData(responseData);
        }).catch(error => {
            setIsLoading(false);
            console.log("getAttributes error -------->" + error);
        })
    }
    const showAttr = () => {
        let arrNameValue = [];
        let arrkeys = Object.keys(data.attrs)
        let obj = {};
        arrkeys.map((key) => {
            obj[data.attrs[key].name] = data.attrs[key].value;
            return '';
        })
        arrNameValue.push(obj);
        console.log("arrNameValue -------->", JSON.stringify(arrNameValue))
        localStorage.setItem('pre_kml_data', JSON.stringify(arrNameValue))
        localStorage.setItem('inputsArray', JSON.stringify(arrSplittedValue))
        return <div className="flex mb-4">
            <div className="w-1/2 m-h-500px p-5">
                {arrNameValue.length > 0 && arrNameValue[0].hasOwnProperty("Approved Longitude") && arrNameValue[0].hasOwnProperty("Approved Latitude") ?
                    <div className="w-full h-full shadow-lg">
                        <a target="_blank" href={`https://earth.google.com/web/search/Arsenal+fc/@${parseFloat(arrNameValue[0]["Approved Latitude"])},${parseFloat(arrNameValue[0]["Approved Longitude"])},9.09858867a,612.98636056d,35y,-40.1316669h,45.00000069t,0`}>Click here for Google Earth</a>
                        <GoogleMapReact
                            bootstrapURLKeys={{ key: "AIzaSyDNeF0TGdzD6PN696-ZWgdP7z9ltW0l1QY" }}
                            options={() => {
                                return {
                                    mapTypeControl: true,
                                    mapTypeId: "satellite"
                                }
                            }}
                            defaultCenter={{
                                lat: parseFloat(arrNameValue[0]["Approved Latitude"]),
                                lng: parseFloat(arrNameValue[0]["Approved Longitude"])
                            }}
                            center={{
                                lat: parseFloat(arrNameValue[0]["Approved Latitude"]),
                                lng: parseFloat(arrNameValue[0]["Approved Longitude"])
                            }}
                            distanceToMouse={() => { }}
                            defaultZoom={arrNameValue ? 18 : 6}
                        >
                            {arrNameValue.map((nameValue) => {
                                return <AnyReactComponent
                                    lat={nameValue["Approved Latitude"]}
                                    lng={nameValue["Approved Longitude"]}
                                />
                            })}
                        </GoogleMapReact>
                    </div>
                    : <div className="w-full h-full shadow-lg relative">
                        <div className="centerItem">
                            <img className="attr_chat mx-auto" draggable={false} src={map} />
                            <p className="text-center mt-3 text-purple-800">Invalid map data</p>
                        </div>
                    </div>
                }
            </div>
            <div className="w-1/2 m-h-500px">
                <div className="max-w rounded w-full shadow  p-5">
                    <p className="text-xl font-bold mb-3">Attributes</p>
                    <Accordion className="border-0" allowZeroExpanded preExpanded={["attributes"]}>
                        {arrNameValue.map((item, index) => {
                            return <AccordionItem className="shadow-md mb-3" key={item.Latitude} uuid="attributes">
                                <AccordionItemHeading className="bg-gray-400 p-2">
                                    <AccordionItemButton className="w-full border-none outline-none focus:border-0 inline-flex">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-arrow-right"><line x1="5" y1="12" x2="19" y2="12"></line><polyline points="12 5 19 12 12 19"></polyline></svg>
                                        &emsp;
                                        {arrNameValue[index].hasOwnProperty("Tag Number") ? arrNameValue[index]["Tag Number"] : arrNameValue[index]["TAG NUMBER"]}
                                    </AccordionItemButton>
                                </AccordionItemHeading>
                                <AccordionItemPanel className="liveView-item-container">
                                    <table class="table-auto w-full">
                                        <tbody>
                                            {Object.keys(item).map((key) => {
                                                return <tr>
                                                    <td class="border px-4 py-2">{key}</td>
                                                    <td class="border px-4 py-2">{item[key]}</td>
                                                </tr>
                                            })}
                                        </tbody>
                                    </table>
                                </AccordionItemPanel>
                            </AccordionItem>
                        })}
                    </Accordion>
                </div>
            </div>
        </div>
    }

    console.log(data);
    return (
        <div>
            <NavBar showLogoutButton={true} showSearch={false}></NavBar>
            {isLoading ?
                <div className="popUpContainer">
                    <div className="loader"></div>
                </div>
                : ""
            }
            <div id="liveView-container">
                <div className="sideBar bg-purple-900">
                    {arrLiveView != null && arrLiveView.length > 0 ? arrLiveView.map((liveView, index) => {
                        return (<div>
                            <button
                                onClick={(e) => fetchSelectedLiveViewData(e, liveView.Name, liveView.Hdl)}
                                className="bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded inline-flex my-2 justify-center w-full">
                                {liveView.Name}
                            </button>
                        </div>)
                    }) : ""}
                </div>
                <div className="liveView-wrapper">
                    {showData ?
                        <div className="relative">
                            <div className="m-4">
                                <label className="inline-block text-gray-700 text-sm font-bold " htmlFor="username">
                                    Selected LiveView:
                                </label>
                                <p className="inline-block ml-4">{selectedLiveViewHdl}</p>
                            </div>
                            {arrTagNumber.length > 0 &&
                                <div className="m-4 mt-0">
                                    <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="username">
                                        Tag Number
                                </label>
                                    <div className="inline-block relative w-1/4">
                                        <select
                                            class="block appearance-none w-full bg-gray-300 border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline"
                                            onChange={(e) => {
                                                getAttributes(e.target.value)
                                            }}>
                                            <option value="">Select</option>
                                            {
                                                arrTagNumber.map((tag) => {
                                                    return <option value={tag}>{tag}</option>
                                                })
                                            }
                                        </select>
                                        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                                            <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" /></svg>
                                        </div>
                                    </div>
                                    <div className="inline-block relative w-1/4 ml-3">
                                        {
                                            data !== undefined && Object.keys(data).length > 0 ?
                                                <a target="_blank" href={`https://${prefixUrl}.pim360.io/object/TAGGED_ITEM/${data.hdl}`}>
                                                    <img width="15%" src={`https://${prefixUrl}.acl360.io/img/pim360-logo.svg`} onError={(e) => {
                                                        console.log("called me")
                                                        console.log(e);
                                                        e.target.src = `https://${prefixUrl}.acl360.io/img/pim360_logo.png`
                                                        // e.src = "hello.png"
                                                    }} />
                                                </a>
                                                : ''
                                        }


                                    </div>
                                </div>

                            }
                            {data !== undefined && Object.keys(data).length > 0 &&
                                <div>
                                    {showAttr()}
                                </div>

                            }
                        </div>
                        :
                        <div className="centerItem">
                            <img draggable={false} width={300} src={search} />
                            <p className="text-center mt-3 text-purple-800">Select the liveview to show the data here</p>
                        </div>
                    }
                </div>
            </div>
        </div>
    );
}

export default LiveView;